<template>
    <div>
        <el-card class="box-card" shadow="always">
            <div slot="header" class="box-card-header">
                <span>学生管理系统 - 登录</span>
            </div>
            <el-form label-width="80px" :model="form">
                <el-form-item label="用户名：">
                    <el-input
                    placeholder="请输入用户名"
                    v-model="form.username"
                    clearable></el-input>
                </el-form-item>
                <el-form-item label="密码：">
                    <el-input
                    placeholder="请输入密码"
                    v-model="form.password"
                    show-password></el-input>
                </el-form-item>
            </el-form>
            <div style="text-align: right;">
                <el-button
                type="primary"
                @click="handleLogin"
                round>登录</el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
import axios from "axios"
export default {
    name: "login",
    data() {
        return {
            form: {
                username: "",
                password: ""
            }
        }
    },
    methods: {
        handleLogin() {
            // 登录操作
            let data = new FormData();
            data.append("username", this.form.username)
            data.append("password", this.form.password)
            axios
            .post("/user/login", data)
            .then(res => {
                var result = res.data
                if(result.code == 200){
                    this.$notify({
                        title: "操作成功",
                        message: result.msg,
                        type: "success"
                    })
                    this.$router.push({path: "/student/dashboard"})
                    return;
                }
                console.log(result)
                this.$notify.error({
                    title: "操作失败",
                    message: result.msg
                })
            })
        }
    }
}
</script>

<style scoped>
    .el-card{
        width: 400px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .box-card-header {
        text-align: center;
    }
</style>